import Surah from '../data/Surah.json';
import Cookies from 'js-cookie';

const Navigation=()=>{

  return(
    <div style={{width:'100%',paddingBottom:10}}>
      <div style={{width:'100%',paddingTop:20,flexDirection:'row',justifyContent:'space-between'}}>
        <div style={{flex:1,fontSize:12}}>
          {window.savedSurah?
          `Last: ${window.savedSurah+'/'+window.savedAyah}`
          :null}
        </div>
        <div style={{flex:1}}>
          <select
          style={{width:100}}
          value={window.surah}
          onChange={(e)=>{
            window.setSurah(e.target.value);
            Cookies.set('surah',e.target.value,{expires:365});
          }}>
            {Surah.map((item)=>(
              <option key={item.id} value={item.id}>{item.id}  {item.namet} {item.name}  {item.nAyah}</option>
            ))}
          </select>
        </div>
        <div style={{flex:1}}>
          <select
          style={{width:30}}
          value={window.language}
          onChange={(e)=>{
            window.setLanguage(e.target.value);
            Cookies.set('language',e.target.value,{expires:365});
          }}>
            <option value="none">None</option>
            <option value="tr">TR  Trke</option>
            <option value="en">EN  English</option>
            <option value="ru">RU   </option>
            <option value="de">DE  Deutsch</option>
            <option value="hi">HI  </option>
            <option value="id">ID  Ind.</option>
            <option value="inh">INH  </option>
            <option value="fa">FA  </option>
            <option value="bn">BN  </option>
            <option value="ta">TA  </option>
            <option value="ur">UR  </option>
          </select>
        </div>
      </div>
      {window.surah!=1&&window.surah!=9?
      <div style={{width:'100%',paddingTop:10,fontFamily:'Hamdullah',fontSize:33}}>
           
      </div>
      :null}
    </div>
  );
}

export default Navigation;
