import {useState,useEffect} from 'react';
import Positions from '../data/Positions.json';
import Arabic from '../data/languages/ar.json';
import Surah from '../data/Surah.json';
import Cookies from 'js-cookie';

const List=()=>{

  const [langData,setLangData]=useState([]);

  useEffect(()=>{
    if(window.language!='none'){
      import(`../data/languages/${window.language}.json`).then((data)=>{
        setLangData(data);
      })
    }
    else{
      setLangData(['\u00A0','\u00A0']);
    }
  },[window.language])

  if(langData[1]){
    return(
      <div style={{width:'100%'}}>
        <div style={{width:'95%',flexDirection:'row-reverse',flexWrap:'wrap'}}>
          {Positions.map((each)=>{
            if(each.surah==window.surah){
              const nextWord=Positions[each.id];
              return(
                <div style={{flexDirection:'row'}}>
                  <div style={{flexDirection:'row'}}>
                  {nextWord?.ayah>each.ayah||nextWord?.surah>each.surah?
                  <label
                  onClick={()=>{
                  window.savedSurah=window.surah;
                  window.savedAyah=each.ayah;
                  Cookies.set('savedSurah',window.surah,{expires:365});
                  Cookies.set('savedAyah',each.ayah,{expires:365});
                  }}
                  style={{fontSize:12,padding:5,color:'chocolate',marginTop:5}}>
                    {`{ ${each.ayah} }`}
                  </label>
                  :null}
                  <div style={{marginRight:4,marginLeft:4}}>
                    <div style={{fontFamily:'Hamdullah',fontSize:35}}>
                      {Arabic[each.id]}
                    </div>
                    {langData[1]?
                    <div style={{fontSize:9,marginTop:-7,color:'wheat',letterSpacing:-0.3}}>
                      {langData[each.id]?langData[each.id]:'\u00A0'}
                    </div>
                    :null}
                  </div>
                  </div>
                </div>
              )
            }
          })}
        </div>
      </div>
    )
  }

}


export default List;
